import React, { useState, lazy, Suspense, useEffect } from 'react';
import { ContentProps } from '../../types';
import BrowserWindow from './BrowserWindow/BrowserWindow';

import './Content.css';
import './Games/Games.css';
import GameSelector from './Games/game-selector';

const Avatar = lazy(() => import('./Avatar/Avatar'));
const Home = lazy(() => import('./Home/Home'));
const AboutMe = lazy(() => import('./AboutMe/AboutMe'));
const Work = lazy(() => import('./Work/Work'));
const Portfolio = lazy(() => import('./Portfolio/Portfolio'));
const Contact = lazy(() => import('./Contact/Contact'));

const Content: React.FC<ContentProps> = ({ name }) => {
  const greetings = name !== '' ? `Hello, ${name}!` : 'Hello, fellow traveler of the interwebz!';
  const [dialogText, setDialogText] = useState<string>(greetings);
  const [currentAction, setCurrentAction] = useState<string>('');
  const [browserOpen, setBrowserOpen] = useState<boolean>(false);
  const [browserMinimized, setBrowserMinimized] = useState<boolean>(false);
  const [isMobile, setIsMobile] = useState<boolean>(window.innerWidth <= 540);
  const [pendingBrowserOpen, setPendingBrowserOpen] = useState<boolean>(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 540);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleNavigate = (action: string) => {
    setCurrentAction(action);

    // Only these actions should open the browser
    const browserActions = [
      'goTo-about',
      'goTo-work',
      'goTo-contact',
      'goTo-portfolio',
      'goTo-games',
    ];

    if (isMobile && browserActions.includes(action)) {
      // For navigation actions on mobile, wait for dialog to complete
      setPendingBrowserOpen(true);
    } else if (!isMobile && browserActions.includes(action)) {
      // On desktop, unminimize the browser immediately
      setBrowserMinimized(false);
    }
    // Other actions (say-hi, say-meow, chatter, etc.) don't open the browser
  };

  const handleDialogComplete = () => {
    // Open browser after dialog completes on mobile
    if (pendingBrowserOpen) {
      // Add a 1 second delay before opening the browser
      setTimeout(() => {
        setBrowserOpen(true);
        setPendingBrowserOpen(false);
      }, 1000);
    }
  };

  const renderRightSideComponent = () => {
    const loader = (
      <div id="loader">
        <i className="spinner icon"></i>
      </div>
    );
    switch (currentAction) {
      case 'goTo-about':
        return (
          <Suspense fallback={loader}>
            <AboutMe setDialogText={setDialogText} setCurrentAction={handleNavigate} />
          </Suspense>
        );
      case 'goTo-work':
        return (
          <Suspense fallback={loader}>
            <Work setDialogText={setDialogText} setCurrentAction={handleNavigate} />
          </Suspense>
        );
      case 'goTo-portfolio':
        return (
          <Suspense fallback={loader}>
            <Portfolio setDialogText={setDialogText} setCurrentAction={handleNavigate} />
          </Suspense>
        );
      case 'goTo-contact':
        return (
          <Suspense fallback={loader}>
            <Contact
              visitor={name}
              setDialogText={setDialogText}
              setCurrentAction={handleNavigate}
            />
          </Suspense>
        );
      case 'goTo-games':
        return (
          <Suspense fallback={loader}>
            <GameSelector
              visitor={name}
              setDialogText={setDialogText}
              setCurrentAction={handleNavigate}
            />
          </Suspense>
        );
      default:
        return (
          <Suspense fallback={loader}>
            <Home setDialogText={setDialogText} setCurrentAction={handleNavigate} />
          </Suspense>
        );
    }
  };

  useEffect(() => {
    const bgMusic = document.querySelector('#bgMusic') as HTMLAudioElement | null;

    if (bgMusic) {
      bgMusic.volume = 0.1;
    }
  }, []);

  return (
    <Suspense
      fallback={
        <div id="loader">
          <i className="spinner icon"></i>
        </div>
      }
    >
      <div className="ui container main">
        <div className="ui stackable grid">
          <div className="seven wide column">
            <div className="left-side-wrapper">
              <Avatar
                visitor={name}
                dialogText={dialogText}
                setDialogText={setDialogText}
                currentAction={currentAction}
                setCurrentAction={handleNavigate}
                onDialogComplete={handleDialogComplete}
                onOpenBrowser={() => {
                  if (isMobile) {
                    setBrowserOpen(true);
                  } else {
                    setBrowserMinimized(false);
                  }
                }}
              />
            </div>
          </div>
          <div className={`nine wide column ${browserOpen ? 'browser-open' : ''}`}>
            <div className="right-side-wrapper">
              <BrowserWindow
                currentSection={currentAction}
                onNavigate={handleNavigate}
                onMinimize={() => {
                  if (isMobile) {
                    setBrowserOpen(false);
                  } else {
                    setBrowserMinimized(true);
                  }
                }}
                onOpen={() => setBrowserOpen(true)}
                isMobile={isMobile}
                browserOpen={browserOpen}
                browserMinimized={browserMinimized}
              >
                {renderRightSideComponent()}
              </BrowserWindow>
            </div>
          </div>
        </div>
      </div>
    </Suspense>
  );
};

export default Content;
