import React from 'react';
import './PageNavigation.css';

interface PageNavigationProps {
  currentSection: string;
  onNavigate: (action: string) => void;
}

const PageNavigation: React.FC<PageNavigationProps> = ({ currentSection, onNavigate }) => {
  const navItems = [
    { id: '', label: 'Home', icon: '' },
    { id: 'goTo-about', label: 'About', icon: '' },
    { id: 'goTo-work', label: 'Work', icon: '' },
    { id: 'goTo-portfolio', label: 'Portfolio', icon: '' },
    { id: 'goTo-contact', label: 'Contact', icon: '' },
    { id: 'goTo-games', label: 'Games', icon: '' },
  ];

  const getActiveClass = (itemId: string) => {
    return currentSection === itemId ? 'active' : '';
  };

  return (
    <div className="page-navigation">
      <div className="nav-container">
        {navItems.map((item) => (
          <button
            key={item.id}
            className={`nav-item ${getActiveClass(item.id)}`}
            onClick={() => onNavigate(item.id)}
            aria-current={currentSection === item.id ? 'page' : undefined}
          >
            <span className="nav-icon">{item.icon}</span>
            <span className="nav-label">{item.label}</span>
          </button>
        ))}
      </div>
    </div>
  );
};

export default PageNavigation;
