import React, { useState, useEffect, useRef } from 'react';
import './BrowserWindow.css';

interface BrowserWindowProps {
  currentSection: string;
  children: React.ReactNode;
  onNavigate: (action: string) => void;
  onMinimize?: () => void;
  onOpen?: () => void;
  isMobile?: boolean;
  browserOpen?: boolean;
  browserMinimized?: boolean;
}

// SVG Icon Components
const BackIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <path d="M10 3L5 8l5 5V3z" />
    <rect x="3" y="7" width="8" height="2" />
  </svg>
);

const ForwardIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <path d="M6 3l5 5-5 5V3z" />
    <rect x="5" y="7" width="8" height="2" />
  </svg>
);

const RefreshIcon = () => (
  <svg
    viewBox="0 0 16 16"
    width="14"
    height="14"
    fill="none"
    stroke="currentColor"
    strokeWidth="1.5"
  >
    <path
      d="M14 8a6 6 0 0 1-6 6 6 6 0 0 1-6-6 6 6 0 0 1 6-6 6 6 0 0 1 5.5 3.5"
      strokeLinecap="round"
    />
    <polyline points="11 2 14 2 14 5" />
  </svg>
);

const StopIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <rect x="4" y="4" width="8" height="8" />
  </svg>
);

const HomeIcon = () => (
  <svg viewBox="0 0 16 16" width="14" height="14" fill="currentColor">
    <path d="M2 8l6-5 6 5v5a1 1 0 0 1-1 1H9v-4H7v4H3a1 1 0 0 1-1-1V8z" />
    <polygon points="8,1 1,7 2,8 8,3 14,8 15,7" />
  </svg>
);

const BrowserWindow: React.FC<BrowserWindowProps> = ({
  currentSection,
  children,
  onNavigate,
  onMinimize,
  onOpen,
  isMobile = false,
  browserOpen = true,
  browserMinimized = false,
}) => {
  const [isMaximized, setIsMaximized] = useState(false);
  const [showBSOD, setShowBSOD] = useState(false);
  const [currentTheme, setCurrentTheme] = useState<string>(() => {
    return localStorage.getItem('browserTheme') || 'classic';
  });
  const [menubarThemeOpen, setMenubarThemeOpen] = useState(false);
  const [statusbarThemeOpen, setStatusbarThemeOpen] = useState(false);
  const menubarRef = useRef<HTMLDivElement>(null);
  const statusbarRef = useRef<HTMLDivElement>(null);

  // Close dropdowns when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menubarRef.current && !menubarRef.current.contains(event.target as Node)) {
        setMenubarThemeOpen(false);
      }
      if (statusbarRef.current && !statusbarRef.current.contains(event.target as Node)) {
        setStatusbarThemeOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // Sync isMaximized with browserOpen on mobile
  useEffect(() => {
    if (isMobile && browserOpen) {
      setIsMaximized(true);
    }
  }, [isMobile, browserOpen]);

  const setTheme = (theme: string, source: 'menubar' | 'statusbar') => {
    setCurrentTheme(theme);
    localStorage.setItem('browserTheme', theme);
    if (source === 'menubar') {
      setMenubarThemeOpen(false);
    } else {
      setStatusbarThemeOpen(false);
    }
  };

  const getSectionUrl = (section: string): string => {
    const urls: { [key: string]: string } = {
      '': 'http://maw.dev/index.html',
      'goTo-about': 'http://maw.dev/about-me.htm',
      'goTo-work': 'http://maw.dev/work/tech-stack.html',
      'goTo-portfolio': 'http://maw.dev/portfolio/gallery.cgi',
      'goTo-contact': 'http://maw.dev/contact/guestbook.php',
    };
    return urls[section] || 'http://maw.dev/';
  };

  const getSectionTitle = (section: string): string => {
    const titles: { [key: string]: string } = {
      '': 'Welcome to Maw.dev - Web Explorer',
      'goTo-about': 'About Me - Web Explorer',
      'goTo-work': 'Tech Stack - Web Explorer',
      'goTo-portfolio': 'Portfolio Gallery - Web Explorer',
      'goTo-contact': 'Contact Form - Web Explorer',
    };
    return titles[section] || 'Maw.dev - Web Explorer';
  };

  const handleBack = () => {
    onNavigate('');
  };

  const handleHome = () => {
    onNavigate('');
  };

  // Handle click on browser when mobile and hidden - opens it
  const handleBrowserClick = () => {
    if (isMobile && !browserOpen) {
      onOpen?.();
      setIsMaximized(true);
    }
  };

  return (
    <div
      className={`browser-window ${isMaximized ? 'maximized' : ''} ${browserMinimized ? 'minimized' : ''}`}
      onClick={handleBrowserClick}
    >
      {/* Title Bar */}
      <div
        className="browser-titlebar"
        onDoubleClick={() => {
          if (!isMobile) {
            setIsMaximized(!isMaximized);
          }
        }}
      >
        <div className="browser-titlebar-text">
          <span className="browser-icon"></span>
          {getSectionTitle(currentSection)}
        </div>
        <div className="browser-titlebar-controls">
          <button
            className="titlebar-button minimize"
            aria-label="Minimize"
            onClick={(e) => {
              e.stopPropagation();
              if (isMobile) {
                // Mobile: only works when maximized, returns to hidden state
                if (isMaximized) {
                  setIsMaximized(false);
                  onMinimize?.();
                }
              } else {
                // Desktop: hide browser completely
                onMinimize?.();
              }
            }}
          >
            _
          </button>
          <button
            className="titlebar-button maximize"
            aria-label="Maximize"
            onClick={(e) => {
              e.stopPropagation();
              if (isMobile) {
                // Mobile when maximized: do nothing
                if (isMaximized) return;
                // Mobile when hidden: maximize
                setIsMaximized(true);
                onOpen?.();
              } else {
                // Desktop: toggle maximize
                setIsMaximized(!isMaximized);
              }
            }}
          >
            
          </button>
          <button
            className="titlebar-button close"
            aria-label="Close"
            onClick={(e) => {
              e.stopPropagation();
              if (isMobile && !isMaximized) {
                // Mobile hidden state: do nothing
                return;
              }
              // Desktop or mobile maximized: trigger BSOD
              setShowBSOD(true);
            }}
          >
            
          </button>
        </div>
      </div>

      {/* Menu Bar */}
      <div className="browser-menubar">
        <span className="menu-item">File</span>
        <span className="menu-item">Edit</span>
        <span className="menu-item">View</span>
        <span className="menu-item">Favorites</span>
        <div className="menu-item-wrapper" ref={menubarRef}>
          <span className="menu-item" onClick={() => setMenubarThemeOpen(!menubarThemeOpen)}>
            Themes
          </span>
          {menubarThemeOpen && (
            <div className="theme-dropdown">
              <button
                className={currentTheme === 'classic' ? 'active' : ''}
                onClick={() => setTheme('classic', 'menubar')}
              >
                Classic
              </button>
              <button
                className={currentTheme === 'vaporwave' ? 'active' : ''}
                onClick={() => setTheme('vaporwave', 'menubar')}
              >
                Vaporwave
              </button>
              <button
                className={currentTheme === 'terminal' ? 'active' : ''}
                onClick={() => setTheme('terminal', 'menubar')}
              >
                Terminal
              </button>
            </div>
          )}
        </div>
        <span className="menu-item">Help</span>
      </div>

      {/* Navigation Bar */}
      <div className="browser-navbar">
        <div className="nav-buttons">
          <button
            className="nav-button back"
            onClick={handleBack}
            disabled={currentSection === ''}
            aria-label="Back"
          >
            <BackIcon />
          </button>
          <button className="nav-button forward" disabled aria-label="Forward">
            <ForwardIcon />
          </button>
          <button className="nav-button refresh" aria-label="Refresh">
            <RefreshIcon />
          </button>
          <button className="nav-button stop" aria-label="Stop">
            <StopIcon />
          </button>
          <button className="nav-button home" onClick={handleHome} aria-label="Home">
            <HomeIcon />
          </button>
        </div>
        <div className="nav-addressbar">
          <input
            type="text"
            value={getSectionUrl(currentSection)}
            readOnly
            className="addressbar-input"
          />
        </div>
        <button className="nav-button go">Go</button>
      </div>

      {/* Content Area */}
      <div className="browser-content">
        <div className={`browser-viewport theme-${currentTheme}`}>{children}</div>
      </div>

      {/* BSOD Easter Egg */}
      {showBSOD && (
        <div className="bsod-overlay" onClick={() => setShowBSOD(false)}>
          <div className="bsod-content">
            <h1>:(</h1>
            <p className="bsod-main-text">Your PC ran into a problem and needs to restart.</p>
            <p>We're just collecting some error info, and then we'll restart for you.</p>
            <p className="bsod-error">CRITICAL_PROCESS_DIED</p>
            <p className="bsod-code">Stop code: 0x000000EF</p>
            <p className="bsod-hint">Click anywhere to restart...</p>
          </div>
        </div>
      )}

      {/* Status Bar */}
      <div className="browser-statusbar">
        <div className="status-text">Done</div>
        <div className="status-icons">
          <div className="status-theme-selector" ref={statusbarRef}>
            <button
              className="status-theme-button"
              onClick={() => setStatusbarThemeOpen(!statusbarThemeOpen)}
              aria-label="Change theme"
            >
               {currentTheme.charAt(0).toUpperCase() + currentTheme.slice(1)}
            </button>
            {statusbarThemeOpen && (
              <div className="status-theme-dropdown">
                <button
                  className={currentTheme === 'classic' ? 'active' : ''}
                  onClick={() => setTheme('classic', 'statusbar')}
                >
                  Classic
                </button>
                <button
                  className={currentTheme === 'vaporwave' ? 'active' : ''}
                  onClick={() => setTheme('vaporwave', 'statusbar')}
                >
                  Vaporwave
                </button>
                <button
                  className={currentTheme === 'terminal' ? 'active' : ''}
                  onClick={() => setTheme('terminal', 'statusbar')}
                >
                  Terminal
                </button>
              </div>
            )}
          </div>
          <span className="status-icon"></span>
          <span className="status-zone">Internet</span>
        </div>
      </div>
    </div>
  );
};

export default BrowserWindow;
